.login {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .login_telegram {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .login_telegram > h1 {
    color: white;
    font-size: 3.5rem;
    margin-top: 10px;
  }
  
  .login_telegram > .login_button {
    margin-top: 60px;
    color: white;
    background-color: #0088cc;
    font-size: 1.1em !important;
    width: 300px;
    font-weight: bold;
    text-transform: capitalize;
  }
  
  .login_telegram > .login__button:hover {
    background-color: white;
    color: #0088cc;
  }
  